#div-content {
	align-content: baseline;
	align-items: baseline;
	align-self: stretch;
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	flex: 1 1;
	width: 1100px;
	margin: auto;
	height: 100%;
}

#markdown-content::-webkit-scrollbar,
.content::-webkit-scrollbar,
#table-article::-webkit-scrollbar {
	background-color: rgb(50 50 50 / .5%);
	border-radius: 19px;
	width: 15px;
	cursor: default;
}

#markdown-content::-webkit-scrollbar-track-piece,
.content::-webkit-scrollbar-track-piece,
#table-article::-webkit-scrollbar-track-piece {
	background-color: rgb(60 60 60 / 15%);
	margin-block: 5px;
	border-radius: 20px;
}

#markdown-content::-webkit-scrollbar-thumb,
.content::-webkit-scrollbar-thumb,
#table-article::-webkit-scrollbar-thumb {
	background-color: rgb(50 41 70 / 40%);
	box-shadow: 1px 1px 2px inset #333;
	border-radius: 1400px;
	border-block: 2px double rgb(150 150 150);
	border-inline: 2px double rgb(150 150 150);
}

	#markdown-content::-webkit-scrollbar-thumb:hover,
	.content::-webkit-scrollbar-thumb:hover,
	#table-article::-webkit-scrollbar-thumb:hover {
		background-color: rgb(150 141 170 / 70%);
		border-block: 2px double rgb(180 10 10);
		border-inline: 2px double rgb(180 10 10);
		cursor: grab;
	}

#markdown-content::-webkit-scrollbar-button,
.content::-webkit-scrollbar-button {
	background-color: rgb(200 20 21 / 70%);
	height: 15px;
	border: 2px double rgb(100 30 30);
	border-radius: 80px 170px 30px 360px;
	display: flex;
	box-shadow: 2px -1px 1px inset #888;
	cursor: pointer;
}

	#markdown-content::-webkit-scrollbar-button:hover,
	.content::-webkit-scrollbar-button:hover {
		background-color: rgb(30 60 110);
		border: 2px double rgb(190 40 41 / 90%);
		cursor: pointer;
	}

.markdownEditor {
	align-items: stretch;
	align-self: flex-end;
	border: 2px solid rgba(60,110,120,.4);
	border-radius: 50px;
	box-shadow: 0px 0px 3px rgb(90 110 120 / 90%);
	cursor: auto;
	display: flex;
	width: -webkit-fill-available;
	width: -moz-available;
	width: 1000px;
	flex-flow: column wrap;
	height: auto;
	justify-content: space-evenly;
	margin-block: 250px;
	order: 1;
	max-width: 1300px;
}

#intro-section {
	align-items: center;
	align-self: center;
	border: 3px groove #7f7f7f;
	border-radius: 30px;
	height: 300px;
	display: flex;
	flex-flow: row nowrap;
	flex: 1 1 1000px;
	justify-content: flex-start;
	justify-self: center;
	margin-top: 2vmin;
	max-width: 600px;
	position: relative;
	cursor: default;
}

#markdown-article {
	align-items: inherit;
	align-content: flex-end;
	background-color: rgba(60,110,120,.4);
	border-block: 2px solid rgba(60,110,120,.4);
	border-radius: 50px;
	box-shadow: -1px -2px 1px rgb(60 110 120 / 50%);
	display: flex;
	flex: 1 1 auto;
	flex-flow: column wrap;
	justify-content: flex-start;
	margin: -95px;
	padding: 5px;
	padding-inline: 5px;
	height: 430px;
}

#fieldset-1 {
	align-content: flex-start;
	align-items: flex-start;
	background-color: rgba(187 195 155 / .6);
	border-radius: 45px;
	height: 290px;
	display: flex;
	flex-flow: column nowrap;
	flex: 2 0 100%;
	justify-content: flex-start;
	justify-items: flex-start;
	order: 0;
	width: 350px;
}

	#fieldset-1 > legend {
		border-radius: 17px 27px 0px 17px;
		margin-block: 5px -4px;
		margin-inline: -15px 0;
		padding-inline: 10px;
		padding-block: 10px 3px;
	}

	#fieldset-1 label {
		padding-bottom: 10px;
		margin-left: 5px;
	}

#markdown-content {
	height: -webkit-fill-available;
	height: -moz-available;
	height: stretch;
	margin-block: 20px 10px;
	border-radius: 10px 10px 30px 30px;
	text-align: initial;
	width: 330px;
	background-color: rgb(245 245 245 / 0.90);
	padding-inline: 10px 0px;
	padding-block: 10px;
	letter-spacing: -.3px;
	word-spacing: -2px;
	font-weight: 600;
	border: 2px solid darkslategray;
	cursor: pointer;
	z-index: 1000000000000;
}

#fieldset-2 {
	align-content: flex-end;
	align-items: center;
	background-color: rgba(177 165 115 / .5);
	border-radius: 45px;
	display: flex;
	flex-flow: column-reverse nowrap;
	flex: 2 0 100%;
	height: 290px;
	justify-content: flex-start;
	order: 1;
	width: 330px;
	overflow-y: auto;
}

	#fieldset-2 > legend {
		border-radius: 27px 17px 17px 0px;
		margin-block: 5px -4px;
		margin-inline: 0px -15px;
		padding-inline: 10px;
		padding-block: 10px 3px;
	}

.content {
	overflow-y: auto;
	margin-block: 70px 10px;
	border-top: 7px ridge rgb(187 187 187);
	border-inline: 5px #ccc ridge;
	border-radius: 10px 10px 1px 10px;
	padding-inline: 10px 5px;
	background-color: rgb(76 138 145 / 60%);
	font-size: 13px;
	width: 320px;
	height: inherit;
	padding-block: 0px;
	font-weight: 600;
}

#multiform-aside {
	position: absolute;
	width: 500px;
	left: 75px;
}

#multiform-nav {
	display: flex;
	flex: 1 1 auto;
	flex-flow: row-reverse;
	width: 500px;
	position: sticky;
	align-items: flex-end;
	margin-inline: 40px;
	margin-block: 0;
	height: 280px;
}

.multi-form-menu {
	width: 50px;
	display: flex;
	flex: 0 1 5%;
	flex-flow: column-reverse;
	height: 50px;
	padding: 0px;
	margin: 0;
	margin-inline: 10px 20px;
	margin-block: 0;
	list-style: none;
}

#table-displayer {
	width: 30px;
	height: 40px;
	border-radius: 10px 3px 3px 9px;
	background-color: rgb(225 215 80 / 0.9);
	border: 1px solid #777;
	box-shadow: -.5px .5px 2px #333;
	cursor: pointer;
	position: absolute;
}

#table-article {
	background-color: rgb(115 125 125 / .5);
	border-radius: 10px 10px 20px 20px;
	box-shadow: 0px 0px 7px #555;
	height: 280px;
	overflow: auto;
	width: 550px;
	margin-block: 100px -100px;
}

	#table-article::-webkit-scrollbar-button {
		background-color: rgb(100 60 80 / .7);
		border-radius: 10px;
		height: 25px;
	}

#product-table {
	border-collapse: collapse;
	display: inline-table;
	background-color: rgb(82 143 150 / 75%);
	border: 2px rgb(20 85 67) solid;
	font-weight: 500;
}

caption {
	background-color: rgb(195 60 50 /.5);
	border-radius: 15px 15px 5px 5px;
	border: 1px ridge rgb(176 196 222);
}

thead > tr {
	border: 2px groove #444;
	height: 30px;
}

#table-body {
	font-size: 12px;
}

tbody > tr {
	height: 40px;
}

.td-prod {
	width: 300px;
	text-indent: 10px;
}

.dragging {
	opacity: .3;
	cursor: grabbing;
}

.dragover {
	opacity: .3;
}

@media all and (max-width: 700px) {
	#intro-section {
		flex-flow: column nowrap;
		justify-content: center;
		height: auto;
	}

	.markdownEditor {
		flex-flow: column nowrap;
		justify-content: center;
		height: auto;
	}

	@media all and (min-width: 300px) {
		#intro-section {
			flex-flow: column nowrap;
			flex: 1 1;
			max-width: 285px;
		}

		.markdownEditor {
			flex-flow: column nowrap;
			flex: 1 1;
			max-width: 285px;
		}
	}
}


#intro-article {
	align-items: center;
	background: rgba(60,110,120,.4);
	border-block: 2px solid rgba(60,110,120,.4);
	border-radius: 30px 5px 5px 30px;
	box-shadow: -1px -2px 1px rgb(60 110 120 / 40%);
	cursor: default;
	display: flex;
	flex: 1 1 auto;
	flex-flow: row wrap;
	height: 290px;
	justify-content: space-evenly;
	margin: auto;
	order: 0;
	overflow-y: scroll;
	text-align: center;
	width: 100%;
}

	#intro-article::-webkit-scrollbar {
		background-color: rgb(50 50 50 / .5%);
		border-radius: 19px;
		width: 15px;
	}

	#intro-article::-webkit-scrollbar-track-piece {
		background-color: rgb(60 60 60 / 15%);
		margin-block: 5px;
		border-radius: 20px;
	}

	#intro-article::-webkit-scrollbar-thumb {
		background-color: rgb(50 41 70 / 40%);
		box-shadow: 1px 1px 2px inset #333;
		border-radius: 1400px;
		border-block: 2px double rgb(150 150 150);
		border-inline: 2px double rgb(150 150 150);
	}

		#intro-article::-webkit-scrollbar-thumb:hover {
			background-color: rgb(150 141 170 / 70%);
			border-block: 2px double rgb(180 10 10);
			border-inline: 2px double rgb(180 10 10);
			cursor: grab;
		}

	#intro-article::-webkit-scrollbar-button {
		background-color: rgb(200 20 21 / 70%);
		height: 15px;
		border: 2px double rgb(100 30 30);
		border-radius: 80px 170px 30px 360px;
		display: flex;
		box-shadow: 2px -1px 1px inset #888;
		cursor: pointer;
	}

		#intro-article::-webkit-scrollbar-button:hover {
			background-color: rgb(30 60 110);
			border: 2px double rgb(190 40 41 / 90%);
			cursor: pointer;
		}

@media all and (max-width: 700px) {
	#intro-article {
		border-radius: 30px 30px 10px 10px;
		flex: 100% 1;
		flex-flow: column wrap;
		height: initial;
	}

	@media all and (min-width: 300px) {
		#intro-article {
			border-radius: 30px 30px 10px 10px;
		}
	}
}

#intro-article > h2 {
	font-feature-settings: "unic";
	background: rgb(214 5 9 / 30%);
	border-bottom: 3px groove rgb(150 150 165);
	border-radius: 35px 50px 30px 20px;
	font-variant: unicase;
	margin-block: 15px 25px;
	padding-bottom: 1px;
	text-indent: -10px;
	text-shadow: 0 0px 2px #254655;
	width: 180px;
	font-size: 18px;
}

@media all and (max-width: 700px) {
	#intro-article > h2 {
		font-size: 14px;
		margin-block: -10px 30px;
		width: 130px;
	}
}

.intro-ps {
	background-color: hsla(0,0%,45%,.3);
	border-radius: 10px;
	font-size: 14px;
	margin-inline: 10px -4px;
	padding-inline: 10px 5px;
	padding-top: 5px;
	text-align: left;
	text-shadow: 0 0 1px #222;
	width: 94%;
}

#intro-p-one {
	margin-block: -5px -15px;
	order: 0;
	height: 130px;
}

#intro-p-two {
	margin-block: 5px 1px;
	height: 110px;
	order: 1;
}

#intro-p-three {
	height: 155px;
	margin-block: -10px 0px;
	order: 2;
}

@media all and (max-width: 700px) {
	.intro-ps {
		font-size: 10px;
	}

	#intro-p-one {
		margin-block: -35px -40px;
		height: 100px;
	}

	#intro-p-two {
		margin-block: 0px -25px;
		height: 85px;
	}

	#intro-p-three {
		margin-block: -15px -5px;
		height: 110px;
	}

	@media all and (min-width: 300px) {
		.intro-ps {
			font-size: 11px;
		}

		#intro-p-one {
			height: 130px;
			margin-block: -40px -15px;
		}

		#intro-p-two {
			height: 130px;
			margin-block: -40px -30px;
		}

		#intro-p-three {
			height: 130px;
			margin-block: -40px -20px;
		}
	}
}

#intro-p-three ~ small {
	font-size: 85%;
}

#content-aside {
	align-items: center;
	background: rgba(46,107,97,.4);
	border-block: 2px solid rgba(46,107,97,.4);
	border-radius: 5px 30px 30px 5px;
	display: flex;
	flex: 1 1 auto;
	height: 290px;
	order: 0;
	width: 100%;
	box-shadow: -1px -2px 1px rgb(46 107 97 / 40%);
}

@media all and (max-width: 700px) {
	#content-aside {
		align-items: stretch;
		border-radius: 10px 10px 30px 30px;
		height: 300px;
	}

	@media all and (min-width: 300px) {
		#content-aside {
			border-radius: 10px 10px 30px 30px;
		}
	}
}

#intro-img {
	border-radius: 70px;
	border-right: 3px inset #717171;
	border-top: 2px outset #717171;
	box-shadow: -3px 2px 0 #717171;
	height: fit-content;
	transform: perspective(10000px);
	flex-grow: 1;
	position: sticky;
	image-rendering: auto;
	margin-block: 10px;
	width: 180px;
	margin-inline: 15px 10px;
}
